



























export default {
	data() {
		return {
			title: 'createSelectorQuery',
			top: 0,
			left: '0px',
			info: []
		}
	},
	onReady() {
		this.getNodeInfo()
	},
	methods: {
		setPosition() {
			this.left = Math.random() * uni.upx2px(320) + 'px'
			this.top = Math.random() * uni.upx2px(320) + 'px'
			this.getNodeInfo()
		},
		getNodeInfo() {
			uni.createSelectorQuery().select('.target').boundingClientRect().exec((ret) => {
				const rect = ret[0]
				if (rect) {
					const info = []
					for (let key in rect) {
						if (key !== 'id' && key !== 'dataset') {
							info.push({
								'key': key,
								'val': rect[key]
							})
						}
					}
					this.info = info
				}
			});
		}
	},
}
